 .banner--context { 
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}

.banner {
    position: relative;
    height: 448px;
    object-fit: contain;
}
.banner--title {
 
    font-size: 3rem;
    font-weight: 800;
    padding: 0.3rem;
}

.banner--desc {
    width: 45rem; 
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem; /* makes it small */
    max-width: 360px;
    height: 80px;
} 

.banner--fade {
     height: 7.4rem;
     background-image: linear-gradient(
         100deg,
         transparent,
        rgba(37, 37, 37, 0.61),
        #111 
     );
}

 .banner--button {
    cursor: pointer;
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);
    padding-bottom: 0.5rem;
}

.banner--button:hover { 
    background-color: white;
    color: #000;
    transition: all 0.2s;
} 